import shopCartDetails from './shopCartDetails.vue';
import { mapActions } from 'vuex';

export default {
	data() {
		return {
			allListData: {},
			fold: true // 购物车详情页折叠状态,true表示折叠
		};
	},
	props: {
		shopInfo: {
			type: Object,
			default: () => {}
		},
		cartSlectList: {
			type: Array,
			default: () => []
		}
	},
	watch: {},
	computed: {
		totalPrice() {
			let total = 0;
			this.cartSlectList.forEach(item => {
				total += item.food_price * item.food_count;
			});
			return total.toFixed(2);
		},
		totalCount() {
			let count = 0;
			this.cartSlectList.forEach(item => {
				count += item.food_count;
			});
			return count;
		},
		minPrice() {
			if (this.shopInfo) {
				return this.shopInfo.float_minimum_order_amount;
			}
		},
		payClass() {
			if (this.totalPrice < this.minPrice) {
				return 'not-enough';
			} else {
				return 'enough';
			}
		},
		payDesc() {
			if (this.totalPrice === 0) {
				return `¥${this.minPrice}元起送`;
			} else if (this.totalPrice < this.minPrice) {
				let diff = this.minPrice - this.totalPrice;
				return `还差${diff}`;
			} else {
				return '去结算';
			}
		}
	},
	components: {
		shopCartDetails
	},
	methods: {
		...mapActions(['setOrderInfo']),
		add(id) {
			this.$emit('add', id);
		},
		sub(id) {
			this.$emit('sub', id);
		},
		toggleList() {
			if (!this.totalCount) {
				return;
			}
			this.fold = !this.fold;
		},
		changeFold() {
			this.fold = true;
		},
		empty() {
			this.$emit('empty');
		},
		pay() {
			if (this.totalPrice < this.minPrice) {
				return;
			}
			this.setOrderInfo({
				shopInfo: this.shopInfo,
				selectFoods: this.cartSlectList
			});
			uni.navigateTo({
				url: '../orders/orders'
			});
		}
	}
};
